  }
  .p-object__item:last-child {
    margin-right: 10px;
  }
  .p-object__image {
    width: 280px;
    height: 280px;
  }
  .p-object__image img {
    height: 280px;
  }
  .p-object__info {
    margin-top: 18px;
    margin-bottom: 10px;
    min-height: 0;
    max-height: 100%;
  }
  .p-object__button {
    display: block;
    background: transparent;
    border: 1px solid #db072f;
    color: #db072f;
    margin-top: 25px;
  }
  .p-object__button:hover {
    color: #fff;
    background: #db072f;
  }
}

.p-form {
  padding: 75px 0 79px;
  background: #fff;
}

.p-form__title {
  text-align: center;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 600;
  color: #363636;
}

.p-form__grid {
  padding-top: 53px;
  display: flex;
  justify-content: space-between;
}

.p-form__grid .input-container {
  width: 32.5%;
}

.p-form .input-container {
  margin-bottom: 18px;
}

.p-form__button {
  max-width: 393px;
  margin: 28px auto 0;
  text-align: center;
}

.p-form__button .btn {
  width: 100%;
  padding: 19px;
}

.p-form__button p {
  font-size: 10px;
  margin-top: 18px;
  letter-spacing: 0.5px;
}

.p-form__button p a {
  color: #db072f;
  border-bottom: 1px solid transparent;
  transition: all 0.25s ease-in-out;
}

.p-form__button p a:hover {
  border-color: #db072f;
}

.p-form .jq-file {
  position: relative;
  display: inline-block;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  max-width: 100%;
  cursor: pointer;
}

.p-form .jq-file:hover .jq-file__browse {
  background: #db072f;
  color: #fff;
}

.p-form .jq-file.changed .jq-file__name {
  color: #363636;
  font-size: 14px;
}

.p-form .jq-file.changed .jq-file__name:after {
  display: none;
}

.p-form .jq-file input {
  position: absolute;
  top: 0;
  left: 0;
  height: auto;
  margin: 0;
  padding: 0;
  opacity: 0;
  font-size: 0;
  height: 100%;
  cursor: pointer;
}

.p-form .jq-file__browse {
  font-size: 14px;
  display: inline-block;
  line-height: 1;
  color: #db072f;
  background: #fff;
  transition: all 0.25s ease-in-out;
  font-family: 'Open Sans', sans-serif;
  padding: 17px 19px;
  border: 1px solid #db072f;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  color: #db072f;
  min-width: 115px;
}

.p-form .jq-file__name {
  border: 1px solid #d7d7d7;
  font-size: 14px;
  font-family: 'Open Sans', sans-serif;
  padding: 16px 19px;
  color: #959595;
  width: 100%;
  resize: none;
  touch-action: manipulation;
  max-height: 50px;
  overflow: hidden;
  margin-right: 15px;
  font-size: 0;
}

.p-form .jq-file__name:after {
  display: inline-block;
  position: static;
  content: 'Файл не выбран (приверепить файл с техническим заданием или проектом)';
  font-size: 14px;
}

@media only screen and (max-width: 788px) {
  .p-form {
    padding: 43px 0;
  }
  .p-form__title {
    font-size: 20px;
    text-align: center;
  }
  .p-form__title span {
    display: block;
  }
  .p-form__grid {
    flex-wrap: wrap;
  }
  .p-form .input-container {
    width: 100%;
  }
  .p-form .input-container textarea {
    min-height: 90px;
    max-height: 90px;
  }
  .p-form .jq-file__name {
    margin-right: -1px;
  }
  .p-form .jq-file__name:after {
    content: 'Файл';
  }
  .p-form .jq-file__browse {
    font-size: 0;
    position: relative;
    min-width: 55px;
    max-width: 55px;
  }
  .p-form .jq-file__browse:after {
    display: inline-block;
    position: static;
    content: '...';
    font-size: 14px;
  }
}
