.page404 {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  position: relative;
  &__logo {
    margin-top: -21px;
    width: 173px;
    height: auto; }
  &__title {
    font-size: 36px;
    color: $fontColor;
    margin-top: 67px; }
  &__desc {
    font-size: 14px;
    color: $fontColor;
    margin-top: 23px; }
  .btn {
    margin-top: 66px;
    padding: 18px;
    max-width: 224px;
    width: 100%; }
  &__image {
    max-width: 1100px;
    width: 55%;
    position: absolute;
    right: 0;
    top: -110px;
    height: auto; } }

@include r($desktop1068) {
  .page404 {
    &__image {
      top: -80px; } }

