@include r($tablet) {
  .page404 {
    &__logo {
      margin-top: -18px;
      position: relative;
      z-index: 2; }
    &__title {
      font-size: 24px;
      margin-top: 41px;
      position: relative;
      z-index: 2; }
    &__desc {
      position: relative;
      z-index: 2;
      max-width: 265px; }
    .btn {
      margin-top: 52px;
      position: relative;
      z-index: 2; }
    &__image {
      top: -50px; } } }

@include r($mobile) {
  .page404 {
    &__title {
      max-width: 140px;
      margin-top: 30px; }

    &__logo {
      width: 140px; }

    &__image {
      width: 55%;
      min-height: 150px; }

