* {
  box-sizing: border-box;
  outline: none; }

h1,
h2,
h3,
h4,
p {
  margin: 0; }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }

body, html {
  background: #ffffff;
  color: $fontColor;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  &.is-fixed {
    overflow: hidden; } }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  // max-width: 1920px
  margin: 0 auto;

  padding-top: 105px;
  &__content {
    // used for sticky footer (flexbox way)
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    // header offset ex
 } }    // padding-top: 100px

@include r($desktop1068) {
  .page {
    padding-top: 75px; } }

@include r($tablet) {
  .page {
    padding-top: 50px; } }

.container {
  max-width: 1280px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px;
 }  // outline: 1px solid red

@include r($mobile) {
  .container {
    padding-left: 20px;
    padding-right: 20px; } }

input {
  -moz-appearance: none;
  -webkit-appearance: none; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

.btn {
  font-size: 14px;
  display: inline-block;
  line-height: 1;
  // padding:
  color: #fff;
  background: $redColor;
  transition: all $transitionSpeed $ease;
  font-family: 'Open Sans', sans-serif;
  padding: 13px;
  text-decoration: none;
  border: none;
  text-align: center;
  cursor: pointer;
  &:hover {
    background: $lightRed; } }

.input-container {
  position: relative;
  input,
  textarea {
    border: 1px solid #d7d7d7;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    padding: 16px 19px;
    transition: all $transitionSpeed $ease;
    color: $fontColor;
    width: 100%;
    resize: none;
    touch-action: manipulation;
    max-height: 50px;
    min-height: 50px;
    overflow: hidden;
    &::-webkit-input-placeholder {
      opacity: 0; }
    &::-moz-placeholder {
      opacity: 0; }
    &:-ms-input-placeholder {
      opacity: 0; }
    &-moz-placeholder {
      opacity: 0; }
    &:not(:placeholder-shown),
    &:focus {
      padding-top: 22px;
      padding-bottom: 10px; }
    // &::-webkit-input-placeholder
    //   transition: all 0.1s $ease
    //   color: #959595
    //   opacity: 0
    //   transition: inherit
    // &:focus::-webkit-input-placeholder
    //   opacity: 1
    // &::-moz-placeholder
    //   transition: all 0.1s $ease
    //   color: #959595
    //   opacity: 0
    // &:focus::-moz-placeholder
    //   opacity: 1
    // &:-ms-input-placeholder
    //   transition: all 0.1s $ease
    //   color: #959595
    //   opacity: 0
    // &:hover:-ms-input-placeholder
    //   opacity: 1
    // &-moz-placeholder
    //   transition: all 0.1s $ease
    //   color: #959595
    //   opacity: 0
    // &:hover-moz-placeholder
    // &:not(:placeholder-shown),
    // &:focus
    //   padding-top: 22px
    //   padding-bottom: 10px
    &:placeholder-shown + label {
      cursor: text;
      position: absolute;
      white-space: nowrap;
      overflow: hidden;
      transform-origin: left top; }
    &:not(:placeholder-shown) + label,
    &:focus + label {
      transform: translate(0, 0);
      font-size: 9px;
      top: 10px;
      left: 2px;
      cursor: pointer;
      color: $fontColor; } }
  label {
    touch-action: manipulation;
    position: absolute;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    padding: 0 19px;
    transition: all $transitionSpeed $ease;
    color: #959595;
    z-index: 2;
    left: 1px;
    top: 17px;
    // transform: translate(0, -50%)
    span {
      color: $redColor; }
    &.error {
      position: absolute;
      // top: 80%
      top: auto;
      bottom: -15px;
      transform: translate(0, 0);
      left: 0;
      background: #fff;
      display: block;
      padding: 6px;
      font-size: 10px;
      color: $redColor;
      border: 1px solid $redColor; } }
  &.has-error {
    input,
    textarea {
      border-color: $redColor; } } }

.radio-container {
  position: relative;
  padding-left: 30px;
  input {
    position: absolute;
    width: 0;
    height: 0;
    left: 0;
    top: 0;
    opacity: 0;
    z-index: -9999;
    &:checked + .radio__text {
      &:after {
        opacity: 1; } } }
  label {
    font-size: 14px;
    color: $fontColor;
    position: relative;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      top: 0px;
      left: -31px;
      width: 19px;
      height: 19px;
      display: block;
      border: 1px solid #333;
      border-radius: 50%; }
    &:after {
      display: block;
      position: absolute;
      content: '';
      width: 11px;
      height: 11px;
      background: $redColor;
      border-radius: 50%;
      left: -26px;
      top: 5px;
      opacity: 0;
      transition: all $transitionSpeed $ease; } } }

.checkbox-container {
  position: relative;
  padding-left: 34px;
  input {
    position: absolute;
    width: 0;
    height: 0;
    left: 0;
    top: 0;
    opacity: 0;
    z-index: -9999;
    &:checked + .radio__text {
      &:after {
        opacity: 1; } } }
  label {
    font-size: 14px;
    line-height: 1.2;
    color: $fontColor;
    position: relative;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      top: 0px;
      left: -33px;
      width: 19px;
      height: 19px;
      display: block;
      border: 1px solid #d7d7d7; }
    &:after {
      display: block;
      position: absolute;
      content: '';
      width: 10px;
      height: 5px;
      border: 3px solid $redColor;
      transform: rotate(-45deg);
      border-top: none;
      border-right: none;
      left: -29px;
      top: 5px;
      opacity: 0;
      transition: all $transitionSpeed $ease; } } }

.nav {
  background: #f1f1f1;
  width: 100%;
  height: 58px; }

.hover-link {
  position: relative;
  transition: all $transitionSpeed $ease;
  display: block;
  width: 100%;
  height: 100%;

  &:hover {
    &:after,
    &:before {
      opacity: 1; } }

  &:after {
    display: block;
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,0.35);
    left: 0;
    top: 0;
    z-index: 2;
    transition: all $transitionSpeed $ease;
    opacity: 0; }
  &:before {
    display: block;
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background-image: url(../img/zoom.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 20%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 4;
    transition: all $transitionSpeed $ease;
    opacity: 0; } }

.video-hover-link {
  position: relative;
  transition: all $transitionSpeed $ease;
  display: block;
  width: 100%;
  height: 100%;

  &:hover {
    &:after,
    &:before {
      opacity: 1; } }

  &:after {
    display: block;
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,0.35);
    left: 0;
    top: 0;
    z-index: 2;
    transition: all $transitionSpeed $ease;
    opacity: 0; }
  &:before {
    display: block;
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background-image: url(../img/play.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 20%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 4;
    transition: all $transitionSpeed $ease;
    opacity: 0; } }


@include r($tablet) {
  .nav {
    display: none; } }




.head {
  h1 {
    font-size: 36px;
    font-weight: 600;
    line-height: 1.25;
    margin: 39px 0;
    color: $fontColor; }
  p {
    font-size: 15px;
    color: $fontColor;
    margin-bottom: 25px;
    span {
      font-weight: 600; } } }

@include r($tablet) {
  .head {
    h1 {
      font-size: 23px;
      margin: 24px 0 31px;
      line-height: 1.3; }
    p {
      font-size: 16px;
      margin-bottom: 20px;
      line-height: 1.5;
      margin-top: -3px; } } }

@include r($tablet) {
  .input-container {
    input,
    textarea {
      max-height: 100%; }
    textarea {} } }



