/*======================================
 *  Selectric v1.13.0
 *====================================== */

.selectric-wrapper {
  position: relative;
  cursor: pointer; }

.selectric-responsive {
  width: 100%; }

.selectric {
  // border: 1px solid #DDD
  // border-radius: 0px
  background: #ffffff;
  position: relative;
  overflow: hidden;
  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 38px 0 10px;
    font-size: 14px;
    line-height: 1;
    font-family: 'Open Sans', sans-serif;
    color: $fontColor;
    padding: 13px 9px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .button {
    display: block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 40px;
    color: transparent;
    font-size: 0;
    text-align: center;
    transform-origin: center center;
    transition: all $transitionSpeed $ease;
    &:before {
      display: block;
      position: absolute;
      content: '';
      background: url(../img/arrow-down.svg) center center no-repeat;
      background-size: contain;
      right: 8px;
      top: 10px;
      width: 10px;
      height: 7px;
      transform: rotate(180deg); }
    &:after {
      display: block;
      position: absolute;
      content: '';
      background: url(../img/arrow-down.svg) center center no-repeat;
      background-size: contain;
      right: 8px;
      bottom: 10px;
      width: 10px;
      height: 7px; } } }

.selectric-focus .selectric {
  border-color: #aaaaaa; }

.selectric-hover .selectric {
  border-color: #c4c4c4;
  .button {
    color: #a2a2a2;
    &:after {
      border-top-color: #a2a2a2; } } }

.selectric-open {
  z-index: 99;
  .selectric-items {
    display: block; }
  // .button
 }  //   transform: rotate(-180deg)

.selectric-disabled {
  filter: alpha(opacity = 50);
  opacity: 0.5;
  cursor: default;
  user-select: none; }

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
  select {
    position: absolute;
    left: -100%; }
  &.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    select {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: none;
      z-index: 1;
      box-sizing: border-box;
      opacity: 0; } } }

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important; }

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important; }

/* Items box */

.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: #E9E9E9;
  z-index: -1;
  .selectric-scroll {
    height: 100%;
    overflow: auto; } }

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%; }

.selectric-items {
  outline: 1px solid #c7c7c7;
  margin-top: 1px;
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 12px;
    line-height: 20px;
    min-height: 20px;
    background: #fff; }
  li {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 14px;
    line-height: 1.4;
    // min-height: 20px
    display: block;
    padding: 10px 20px;
    color: #252525;
    cursor: pointer;
    // border-bottom: 1px solid #979797
    &.selected {
      background: #E0E0E0;
      color: #444; }
    &.highlighted {
      background: #D0D0D0;
      color: #444; }
    &:hover {
      background: $redColor;
      color: #fff; } }
  .disabled {
    filter: alpha(opacity = 50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    user-select: none; }
  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      user-select: none;
      background: none;
      color: #444; }
    &.disabled li {
      filter: alpha(opacity = 100);
      opacity: 1; }
    li {
      padding-left: 25px; } } }

@include r($tablet) {
  .selectric-items {
    position: static; } }
