.photo {
  padding-bottom: 100px;
  &__head {
    h1 {
      font-size: 36px;
      font-weight: 600;
      line-height: 1.25;
      margin: 39px 0 30px;
      color: $fontColor; }
    p {
      font-size: 14px;
      color: $fontColor;
      margin-bottom: 57px; } }
  &__item {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .btn {
      background: #fff;
      color: $redColor;
      border: 1px solid $redColor;
      font-size: 12px;
      padding: 14px 13px;
      &:first-child {
        width: 60%;
        margin-right: 10px; }
      &:last-child {
        width: 40%; }
      &:hover {
        background: $redColor;
        color: #fff; } } }
  &__left {
    max-width: 700px;
    width: 59%; }
  &__image {
    max-width: 700px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 100%;
      height: auto; } }
  &__right {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 41%;
    padding: 0 0 0 4%; }
  &__grid {
    max-width: 340px;
    width: 100%; }
  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 35px 0;
    p {
      font-size: 16px;
      color: $fontColor;
      margin-right: 10px; }
    a {
      margin-left: 10px;
      font-size: 12px;
      border-bottom: 1px solid transparent;
      transition: all $transitionSpeed $ease;
      color: $redColor;
      &:hover {
        border-color: $redColor; } } }
  &__button {
    padding-top: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  &__btn-middle {
    max-width: 450px;
    width: 100%;
    margin: 59px auto 0;
    .btn {
      width: 100%;
      padding: 18px; } } }

@include r($desktop) {
  .photo {
    &__left {
      width: 55%; }
    &__right {
      width: 45%;
      padding: 0 0 0 4%; }
    &__row {
      margin: 25px 0;
      p {
        font-size: 15px; } }
    &__button {
      padding-top: 15px; } } }

@include r($tablet) {
  .photo {
    &__head {
      h1 {
        font-size: 23px;
        margin: 24px 0 22px;
        line-height: 1.3; }
      p {
        font-size: 13px;
        line-height: 1.55;
        margin-bottom: 24px; } }
    &__item {
      flex-wrap: wrap; }
    &__left {
      width: 100%; }
    &__right {
      width: 100%;
      padding: 0; } } }

@include r($mobile) {
  .photo {
    padding-bottom: 51px;
    &__left {
      margin: 0 -20px;
      width: calc(100% + 40px); }
    &__row {
      margin: 23px 0 0; }
    &__button {
      flex-wrap: wrap;
      padding-top: 36px;
      .btn {
        &:nth-child(1) {
          width: 100%;
          margin-right: 0; }
        &:nth-child(2) {
          width: 100%;
          margin-top: 15px; } } }
    &__btn-middle {
      max-width: 100%;
      margin: 50px auto 0; } } }

